<template lang="pug">
  .navigator
    transition(name="nav" mode="out-in")
      component(:is="component" @board-tap="openTapHandler" @save-close="closeSaveHandler" @save-game="saveGameHandler")
</template>
<script>
import Board from './Board';
import SaveAndLog from './SaveAndLog';
import { mapActions } from 'vuex';
export default {
  name: 'Navigator',
  data(){
    return {
      component: 'board'
    }
  },
  components: {
    'board': Board,
    'save-and-log': SaveAndLog,
  },
  methods:{
    ...mapActions(['saveGame']),
    openTapHandler(){
      this.component = 'save-and-log';
    },
    closeSaveHandler(){
      this.component = 'board';
    },
    saveGameHandler(){
      this.saveGame();
      this.component = 'board';
    }
  }
}
</script>
<style lang="scss" scoped>
  .navigator{
    height: 30px;
  }
  .nav-enter-active,
  .nav-leave-active{
    transition: all .1s;
  }
  .nav-enter{
    opacity: 0;
    transform: translateX(200px);
  }

  .nav-leave-to{
    opacity: 0;
    transform: translateX(-200px);
  }
</style>